import React, { useRef, useEffect, useState } from 'react';
import { observer } from 'mobx-react-lite';
import userStore from '../../../../store/UserStore';
import { getAllTypesSum, getSum } from '../../../../utils/transactions';
import { getDateName } from '../../../../utils/dates';
import Chart from '../../../../components/Chart/Chart';
import DateStore from '../../../../store/DateStore';
import FilterStore from '../../../../store/FilterStore';
import send from '../../../../img/archiveChart/send.svg';
import topup from '../../../../img/archiveChart/topup.svg';
import withdraw from '../../../../img/archiveChart/withdraw.svg';
import transfers from '../../../../img/archiveChart/transfers.svg';
import { getMoneyString, getPercentString } from '../../../../utils/money';
import { UI_MOBILE } from '../../../../utils/consts';
import UI from '../../../../store/UI';
import './ArchiveChart.scss';

const ArchiveChart = observer(() => {
  const [data, setData] = useState([]);
  const [startDate, setStartDate] = useState();
  const [endDate, setEndDate] = useState();
  const [sumInRangeDates, setSumInRangeDates] = useState(0);
  const [dimensions, setDimensions] = useState({
    width: 370,
    height: 370,
    radius: 100
  });
  useEffect(() => {
    if(UI.device === UI_MOBILE) {
      setDimensions({
        width: 300,
        height: 300,
        radius: 80
      });
      setData([...data]);
    } else {
      setDimensions({
        width: 370,
        height: 370,
        radius: 100
      });
      setData([...data]);
    }
  }, [UI.device]);
  
  useEffect(() => {
    if(FilterStore.filteredByDate) {
      if(DateStore.firstDate < DateStore.secondDate) {
        setStartDate(getDateName(DateStore.firstDate))
        setEndDate(getDateName(DateStore.secondDate));
      }
      else {
        setStartDate(getDateName(DateStore.secondDate))
        setEndDate(getDateName(DateStore.firstDate));
      }
    } else {
      const today = new Date().toLocaleDateString('en-ca');
      const dateOfFirstMadedTransaction = userStore.transactions ? userStore?.transactions[0]?.date : null;
      setEndDate(getDateName(today));
      setStartDate(getDateName(dateOfFirstMadedTransaction))
    }
    if(FilterStore.transactions) {
      const data = getAllTypesSum(FilterStore.transactions);
      setSumInRangeDates(getSum(data));
      setData(data);
    }
  }, [FilterStore.filteredByDate, FilterStore.transactions])

  const statistics = [
    {type: 'payment_made', title: 'Send money', icon: send},
    {type: 'payment_fill', title: 'Top up', icon: topup},
    {type: 'payment_withdraw', title: 'Withdraw', icon: withdraw},
    {type: 'payment_received', title: 'Tranfers', icon: transfers}
  ]
  return (
    <div className='chartArchive'>
      <div className='chartArchive__content'>
        <Chart 
          dimensions={dimensions} data={data} />
        <div className='chartArchive__info'>
          <div className='chartArchive__date'>{startDate} - {endDate}</div>
          <div className='chartArchive__money'>{getMoneyString(sumInRangeDates, userStore.currency)} /
            <span className='chartArchive__money-percent'>
              {getPercentString(userStore.sendedMoney, userStore.limit)}
            </span>
          </div>
          <div className='chartArchive__subtitle'>Send money</div>
          <div className='chartArchive__statistics'>
            {
              statistics.map(statistic => {
                return (
                  <div className='chartArchive__statistic'>
                    <img src={statistic.icon} alt={statistic.icon} />
                    <p className='chartArchive__statisticTitle'>{statistic.title}</p>
                    <div className='chartArchive__money chartArchive__money-statistic'>
                      {getMoneyString(data.find(d => d.type === statistic.type)?.value)} |
                      <span className='chartArchive__money-percent'> {
                        getPercentString(data.find(d => d.type === statistic.type)?.value,
                          data.reduce((accumulator, current) =>  accumulator + current.value, 0))
                        }
                      </span>
                    </div>
                  </div>
                )
              })
            }
          </div>
        </div>
      </div>
    </div>
  );
});

export default ArchiveChart;