export const getSum = (transactionsByDate) => {
  let sum = 0;
  transactionsByDate.forEach(transactionByDate => {
    switch(transactionByDate.type) {
      case "payment_fill":
        sum += +transactionByDate.value;
        break;
      case "payment_made":
        sum -= +transactionByDate.value;
        break;
      case "payment_received":
        sum += +transactionByDate.value;
        break;
      case "payment_withdraw":
        sum -= +transactionByDate.value;
        break;
      default:
        console.log("Out of RANGE");
    }
  });
  return sum;
}

export const getAllTypesSum = (transactionsByDate) => {
  let fillSum = 0;
  let madeSum = 0;
  let receivedSum = 0;
  let withdrawSum = 0;
  transactionsByDate.forEach(transactionByDate => {
    switch(transactionByDate.type) {
      case "payment_fill":
        fillSum += +transactionByDate.value;
        break;
      case "payment_made":
        madeSum += +transactionByDate.value;
        break;
      case "payment_received":
        receivedSum += +transactionByDate.value;
        break;
      case "payment_withdraw":
        withdrawSum += +transactionByDate.value;
        break;
      default:
        console.log("Out of RANGE");
    }
  });
  return [
    {type: 'payment_fill', value: fillSum},
    {type: 'payment_made', value: madeSum},
    {type: 'payment_received', value: receivedSum},
    {type: 'payment_withdraw', value: withdrawSum}
  ];
}