import React, { useRef, useEffect, useState } from 'react';
import './Chart.scss';
import * as d3 from 'd3';
import send from './img/send.svg';
import { useNavigate } from 'react-router-dom';
import { TRANSFERS_ROUTE, TRANSFERS_SEND_ROUTE } from './../../utils/consts';

const Chart = ({dimensions, data}) => {
  const navigate = useNavigate();

  const chartRef = useRef(null);


  useEffect(()=> {
    if(chartRef.current.children[0]) chartRef.current.children[0].remove()

    // Get positions for each data object
		const piedata = d3.pie().value(d => d.value)(data);
    
		// Define arcs for graphing 
		const arc = d3.arc().innerRadius(dimensions.radius).outerRadius(dimensions.width / 2);
		const colors = d3.scaleOrdinal(['#1c3c68','#7084fc','#976E30','#88f850'])

		// Define the size and position of svg
		const svg = d3.select(chartRef.current)
            .attr('width', dimensions.width)
            .attr('height', dimensions.height)
            .append('g')
              .attr('transform',`translate(${dimensions.width / 2}, ${dimensions.height / 2})`)

		// Draw pie
		svg.append('g')
			.selectAll('path')
			.data(piedata)
			.join('path')
				.attr('d', arc)
				.attr('fill',(d, i)=>colors(i))
        .on('mouseover', ()=>console.log('fdsfsd'))
				.on('mousemove', ()=>console.log('fdsfsd'))
				.on('mouseout',()=>console.log('fdsfsd'))  
  }, [data]);

  return (
    <div className='chart'>
      <svg className='chart__svg' ref={chartRef}></svg>
      <div id='chart' className='chart__center' style={{width: dimensions.width, height: dimensions.height}}>
        <div className='chart__circle' style={{width: dimensions.radius * 2, height: dimensions.radius * 2}}>
          <img src={send} alt='send' className='chart__icon' onClick={() => navigate(TRANSFERS_ROUTE + '/' + TRANSFERS_SEND_ROUTE)}/>
          <div className='chart__iconTitle'>Send money</div>
        </div>
      </div>
    </div>
  );
};

export default Chart;