export const getMoneyString = (money, currency = '$') => {
  return money === 0 || !money
          ? `${currency + 0}` 
          : money >= 0 
            ? `+${currency + money}` 
            : `-${currency + Math.abs(money)}`
}

export const getPercentString = (numerator, denominator, numOfDigitsAfterDot = 2) => {
  if(!numerator || !denominator) return '0%'
  return `${(numerator / denominator * 100).toFixed(numOfDigitsAfterDot)}%`
}